import React from 'react';
import Penguin from './Penguin/penguin';
import Giraffe from './Giraffe/giraffe';

const appStyle = {
	textAlign: "center",
	fontFamily: 'Hind, sans-serif',
	width: '100%',
}

const headerStyle = {
	position: 'relative',
	margin: 'auto',
  backgroundColor: '#4fd8ff',
	height: '1500px',
	zIndex: '-10000'
}

const headerText = {
	margin: '0px',
}

const animation = {
	height: '300px'
}
		
const Header = props => (
	<div style={appStyle}>
		<div style={headerStyle}>
			<h1 style={headerText}>Front-end React Developer</h1>
			<p style={headerText}>I build websites using React.js</p>
			<div style={animation} />
			<Penguin />
			<Giraffe />
		</div>
	</div>
);

export default Header;